.login{
    background-color: black;
    display: grid;
    place-items: center;
}
.login > img {
    width: 90%;
    height: auto;
}
.login > a{
    text-decoration: none;
    background-color: #1db954;
    color: white;
    font-weight: 600;
    border-radius: 99px;
    padding: 18px;
}

@media(max-width:600px){
    .login{
        margin-top: 50%;
    }
}