@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: black;
}

.login{
    background-color: black;
    display: grid;
    place-items: center;
}
.login > img {
    width: 90%;
    height: auto;
}
.login > a{
    text-decoration: none;
    background-color: #1db954;
    color: white;
    font-weight: 600;
    border-radius: 99px;
    padding: 18px;
}

@media(max-width:600px){
    .login{
        margin-top: 50%;
    }
}
.check{
    background-color: blueviolet;
    color: white;
}
.menu_Bar{
    height: 100vh;
    background-color: #040404;
}
@media(max-width:600px){
    .menu_Bar{
        display: none;
    }
}
.menuBar_logo{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    height: auto;
}
.menu_Bar > strong {
    margin-left: 10%;
    color: whitesmoke;
}
.menu_navigation{
    color: grey;
}
.menu_navigation:hover{
    display: block;
    text-decoration: none;
    color: whitesmoke;
}
.navigation_active{
    display: block;
    color: whitesmoke;
}
hr{
    border-top: 3px solid whitesmoke;
    width: 80%;
    margin: auto;
    margin-bottom: 10px;
}

.menuBarOptions{
    display: flex;
    align-items: center;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;
}
/* .menuBarOptions:hover{
    color: white;
} */
.menuBarOption_Icon{
    margin: 1px 20px 8px 10%;
}

.menuBarOptions > p{
    margin-left: 10%;
}
.main_window{
    height: 100vh;
    background: linear-gradient(rgb(91, 87, 115),#191414,rgba(0,0,0,1));
    padding: 4%;
}
.header{
    display: flex;
    justify-content: space-between;
}
.header_left{
    background-color: white;
    color: gray;
    border-radius: 30px;
    display: flex;
    align-items: center;
    padding: 10px;
    flex: 1 1;
    max-width: 400px;
}
.header_left > input{
    border: none;
    width: 100%;
}
.header_right{
    display: flex;
    align-items: center;
    color: whitesmoke;
    justify-content: space-between;

}
.header_right>h5{
    color: white;
    margin-bottom: 0;
    margin-left: 20px;
}

form{
    width: 100%;
}
input,textarea{
    width: 100%;
    outline: none !important;
    border: 0px !important;
}
input:focus, textarea{
    outline: none !important;
    border: 0px !important;
}
::-webkit-scrollbar{
    border-radius: 10px;
    height: 8px;
    width: 0px;
    background-color: black ;
  }
 ::-webkit-scrollbar:hover{
    box-shadow: inset 0 0 5px #191414; 
    background-color: #191414;
}
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #191818;
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: black; 
    height: 10px;
  }

/* Main CSS */
.dataslider{
    margin-top: 50px;    
}
.dataslider_outerbox{
    height: 260px;
    display: flex;
    overflow-x: auto;
    margin-top: 30px;
    padding-bottom: 10px;
}
.dataslider_innerbox{
    width: auto;
    height: auto;
    padding: 15px;
    margin-right: 20px;
    background-color: #191414;
    border-radius: 5px;
    cursor: pointer;
    transition: 200ms background-color ease-in;
}
.dataslider_innerbox  h6{
    margin-bottom: 0px;
    color: white;
}
.head_img{
    width: 150px;
    margin-bottom: 10px;
}
.dataslider_innerbox p{
    margin-bottom: 20px;
    color: gray;
}

.dataslider_title{
    color: white;
}
.dataslider_innerbox:hover{
    background-color: #282828;
}
.footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: auto;
}

.music_Player{
    height: 65px;
    background-color: #282828;
    width: 100%;
    display: flex;
    justify-content: space-between;
    color: antiquewhite;
    align-items: center;
}

.disp{
    display: none;
}

.music_Player__left{
    flex: 0.3 1;
    text-align: left;
    display: flex;
    margin-left: 3%;
    align-items: center;

}
.music_Player__left>img{
    height: 100%;
    margin-top: 1px;
}
.music_Player__center{
    flex: 0.4 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;
    margin: auto;
    padding: 0 40px;
}
.music_Player__right{
    flex: 0.3 1;
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding-right: 30px;
}
.musicplayer_responsive_right{
    flex: 0.1 1;
    display: none;
    text-align: center;
    margin-right: 2%;
}
.musicplayer_green:hover{
    color: #1ed15e;
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
}
.musicplayer_icon:hover{
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
}
.musicPlayer__song{
    margin-left: 20px;

}
.musicPlayer__song > h6{
    margin-bottom: 0;
}
.musicPlayer__song > p{
    margin-bottom: 0;
}

.music_Player__right * .MuiSlider-root {
    color: #1ed15e;
}


/* Media Queries <600px*/
@media(max-width:600px){
    .disp{
        display: flex;
    }
    .hide{
        display: none;
    }
    .music_Player__left{
        flex: 0.9 1;
    }
    .music_Player__center{
        display: none;
    }
    .musicplayer_responsive_right{
        display: block;
    }
    .music_Player__right{
        display: none;
    }

}

.menu_Bottom {
    background-color: black;
    height: 65px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu_Bottom div {
    width: 100%;
    flex-grow: 1;
    flex-basis: 0;
    text-align: center;
    color: grey;
    height: 100%;
}
.menu_Bottom_name{
    font-size: 10px;
    margin-bottom: 0;
}

.navigation{
    text-decoration: none;
    display: block;
    color: grey;
    height: 100%;
    padding-top: 9%;
}
.navigation:hover{
    background-color: #191919;
    text-decoration: none;
    color: white;
}
.activeClass{
    background-color: #191919;
    text-decoration: none;
    color: white;
}
@media(min-width:600px){
    .menu_Bottom{
        display: none;
    }
}

.search_layout{
    height: 100vh;
    background: linear-gradient(rgb(102, 87, 115),rgba(0,0,0,1));
    padding: 4%;
    padding-top: 40px;
    overflow-y: scroll;
}
h2{
    color: whitesmoke;
    margin-top: 30px;
    margin-bottom: 20px;
}
.search_heading {
    color: whitesmoke;
    margin-top: 30px;
    font-size: 3em;
}

.hr_search{
    width: 100%;
    border-top: solid whitesmoke;
    margin-bottom: 30px;
}

.search_List{
    list-style-type:none;
    padding: 0 3%;
}

.search_List li{
    display: flex;
    color: rgb(211, 210, 210);
    margin: 10px 0;
    background-color: rgba(25, 20, 20,0.6);
    padding: 1.3% 8px;
    /* box-shadow: 4px 6px 10px black; */
    border-radius: 7px;
    overflow: hidden;
    height: 70px;
    transition: all 0.4s ease;

}

/* .search_Music{
    
} */
.search_Music h6{
    margin-bottom: 0;
    overflow: hidden;

}

.search_Music p{
    margin-bottom: 0;
}

.audioIcon{
    margin-left: 7px;
    margin-right: 7px;
}


.play_Hover{
    height: 100%;
    margin-left: auto;
    margin-right: 15px;
}

.playIconHover{
    opacity: 0;
    color: #1DB954;
    transition: opacity 0.4s ease;
    font-size: x-large;
}
.search_List li:hover .playIconHover{
    opacity: 1;
}
.search_List li:hover{
    opacity: 1;
    transform: scale(1.02);
}


@media(max-width:600px){
    .search_heading{
        font-size: 1.5em;
    }
    .search_List li{
        height: 65px;
    }
    .play_Hover{
        display: none;
    }
}
.library{
    height: 100vh;
    background: linear-gradient(rgb(99, 23, 23),rgba(0,0,0,1));
    padding: 4%;
    padding-top: 40px;
    overflow-y: scroll;
}

.library_heading {
    color: whitesmoke;
    margin-top: 30px;
    font-size: 3em;
}

.hr_library{
    width: 100%;
    border-top: solid whitesmoke;
    margin-bottom: 30px;
}
